

      .colorSquare {
        box-shadow: 0px 0px 25px 0px #fff;
        width: 242px;
        height: 242px;
        margin-bottom: 15px;
      }

      .colorArea input {
        padding: 10px;
        font-size: 16px;
        border: 2px solid #CCC;
      }

      .colorArea button {
        padding: 10px;
        font-size: 16px;
        margin: 10px;
        background-color: #666;
        color: #FFF;
        border: 2px solid #666;
      }

      .colorArea button:hover {
        background-color: #111;
        border-color: #111;
        cursor: pointer;
      }

      .colorArea p {
        color: #FFF;
      }